import React, {Component} from "react";
import {NavLink} from "react-router-dom"
import LSButton from "../../components/login_singnin_button/lsButton"
import Logo from "../../components/pasika_logo/pasikaLogo"
import SocialRef from "../../components/social_reference/socialRef"
import MainPage from "./mainPage"
import "./main_page_structure.css"
import "../../styles/frame.css"




class MainPageFrame extends Component {
    constructor(props){
        super(props);
        this.state = {
            login: false,
            signin: false
        }
    }

    render() {
        return (
            <div className="main-page-container" style={{zIndex: -1}} >
                    <div className="frame-center">
                        <div className={"ls-container"}>
                            <div className={"ls-group"}>
                                <LSButton name={"LOG IN"}
                                          bgcolor={this.state.login ? "black" : "white"}
                                          color={this.state.login ? "white" : "black"}
                                          onMouseOver={() => {this.setState({login: true})}}
                                          onMouseOut={() => {this.setState({login: false})}}
                                />
                                <LSButton
                                    name={"SIGN IN"}
                                    bgcolor={this.state.signin ? "black" : "white"}
                                    color={this.state.signin ? "white" : "black"}
                                    onMouseOver={() => {this.setState({signin: true})}}
                                    onMouseOut={() => {this.setState({signin: false})}}
                                />
                            </div>
                        </div>
                        <MainPage class={"frame-center-item"}>

                        </MainPage>
                    </div>
                     <div className="frame-bottom" style={{zIndex: 2}}>
                         <div> CONTACT US</div>
                         <div className={"frame-bottom-group"}>
                             <SocialRef href={"#"}>FACEBOOK</SocialRef>
                             <SocialRef href={"https://www.instagram.com/pasikamagazine/"}>INSTAGRAM</SocialRef>
                         </div>
                     </div>
                     <div className={"logo-container"} style={{zIndex: 1}}>
                         <Logo/>
                     </div>

            </div>
        );
    }
}


export default MainPageFrame;


