import React from "react";


const Card = (props) => {
    const backgoundStyle = {
        backgroundColor: "black",
        width: "230px",
        height: "280px",
        display: "flex",
        flexDirection: "column",
        marginLeft: "50px"
    };
    const cardStyle = {
        backgroundColor: "white",
        width: "230px",
        height: "230px",
        border: "1px solid black",
        position: "relative",
        top: "5px",
        right: "7px"
    };
    const cardBottom = {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-between"
    };
   return(
       <div style={backgoundStyle}>
           <div style={cardStyle}>
           </div>
           <div style={cardBottom}>
               <div style={{width: "100px",height: "35px", backgroundColor: "white", marginTop: "20px", border: "1px solid black" }}></div>
               <div style={{ fontFamily: "Montserrat, sans-serif", color: "white", marginTop: "30px", marginRight: "15px" }}>01/01/2019</div>
           </div>

    </div>
   )
};


export default Card;


