import React, { Component } from "react";
import MainMenu from "./components/main_menu/menuframe";
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import NoFound from "./components/404NoFound";
import MainPageFrame from "./containers/main_page/mainPage_frame";
import Art from "./containers/art/art_frame";
import Music from "./containers/music/music_frame";
import Fashion from "./containers/fashion/fashion_frame";
import Honey from "./containers/honey/honey_frame";
import "./styles/App.css"
import RegistrationScreen from "./containers/registration_screen/registrationScreen";

class App extends Component {
  render() {
    return (
        <Router>
              <div className="App">
                <MainMenu/>
                <Route render={({location})=> (
                    <Switch location={location}>
                      <Route exact path="/pasika" component={MainPageFrame}/>
                      <Route path="/art" component={Art}/>
                      <Route path="/music" component={Music}/>
                      <Route path="/fashion" component={Fashion}/>
                      <Route path="/honey" component={Honey}/>
                      <Route component={NoFound}/>
                    </Switch>
                )}/>
              </div>
        </Router>

    );
  }
}

export default App;





