import React, { Component } from "react";
import "../../styles/menuframe.css";
import Logo from ".././pasika_logo/pasikaLogo"
import MenuItem from "../menu_item/menuItem"
class MainMenu extends Component {
    constructor(){
        super();
        this.state = {
        }
    }
    render() {
        return (
            <div className={"menu-container"}>
                <MenuItem to={"/art"}
                          className={"menu-item"}>ART</MenuItem>
                <MenuItem to={"/fashion"}
                          className={"menu-item"}>FASHION</MenuItem>
                <Logo/>
                <MenuItem to={"/music"}
                          className={"menu-item"}>MUSIC</MenuItem>
                <MenuItem to={"/honey"}
                          className={"menu-item"}>HONEY</MenuItem>
            </div>
        );
    }
}

export default MainMenu;





