import React from "react"
import "./button.css"


const SimpleButton = (props) => {

    const simpleLayer1 = {
        width: '90px',
        height: '35px',
        backgroundColor: 'black'
    };
    const simpleLayer2 = {
        width: '90px',
        height: '35px',
        backgroundColor: 'none',
        border: '1px solid black',
        position: 'relative',
        top: '3px',
        left: '3px',
    };
    const simpleLayer3 = {
        width: '85px',
        height: '30px',
        backgroundColor: 'black',
        borderTop: '1px solid white',
        borderLeft: '1px solid white',
        position: 'relative',
        top: '-1px',
        left: '-1px',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        fontFamily: 'Montserrat, sans-serif',
        fontWeight: '900',
        color: 'white'
    };


    return(
        <div style={simpleLayer1}>
            <div style={simpleLayer2}>
                <div style={simpleLayer3}>
                    {props.children}
                </div>
            </div>
        </div>
    )

};


export default SimpleButton;


