import React, {Component} from "react";
import "./adv_area.css"
import SimpleButton from "../../components/button/button"


class AdvArea extends Component{
    render(){
        return(
            <div className={"adv-container"}>
                <div className={"greeting"}>{ "Happy New Year PASIKA Team !!! <3" }</div>
                <div className={"adv-btn-container"}>
                    <SimpleButton side={'left'}>PREV.</SimpleButton>
                    <SimpleButton side={'right'}>NEXT.</SimpleButton>
                </div>
            </div>
        )
    }
}

export default AdvArea;


