import React, {Component} from "react";

const LSButton = (props) => {
    const styles = {
        width: "90px",
        height: "30px",
        backgroundColor: props.bgcolor,
        color: props.color,
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        cursor: "pointer",
        fontFamily: "Montserrat, sans-serif",
        fontWeight: "bold"
    };

return (
    <div style={styles}  onMouseOver={props.onMouseOver} onMouseOut={props.onMouseOut}>
        {props.name}
    </div>
)
};


export default LSButton;


