import React, {Component} from "react";
import "./logo_styles.css"

const Logo = (props) => {
    return (
        <div className={"menu-item pasika-container"}>
            <div className={"pasika"}>PASIKA</div>
            <div className={"magazine"}>magazine</div>
        </div>
    )
};


export default Logo;



