import React from "react";
import "./social_ref.css"

const SocialRef = (props) =>(
    <div className={"social-ref-item"}>
        <a target="_blank" rel="noopener noreferrer" href={props.href}>
            {props.children}
        </a>
    </div>
);

export default SocialRef;


