import React, {Component} from "react";
import Card from "../../components/card/card"
import "./card_container.css"




class CardContainer extends Component{
    render(){
        return(
            <div className={"card-container"}>
                <Card/>
                <Card/>
                <Card/>
                <Card/>
            </div>
        )
    }
}

export default CardContainer;


